import { createId } from "@paralleldrive/cuid2";
import { users } from "@user/entities/user.entity";
import { relations } from "drizzle-orm";
import { AnyPgColumn, pgTable, timestamp, varchar } from "drizzle-orm/pg-core";

export const questions = pgTable("questions", {
  id: varchar("id").primaryKey().$default(createId),
  userId: varchar("user_id").notNull().references((): AnyPgColumn => users.id),
  type: varchar("type").notNull(),
  correctAnswer: varchar("correct_answer").notNull(),
  answers: varchar("answers").notNull(),
  title: varchar("title").notNull(),
  description: varchar("description").notNull(),
  createdAt: timestamp("created_at").defaultNow(),
  updatedAt: timestamp("updated_at").defaultNow(),
});

export const questionRelations = relations(questions, ({ one }) => ({
  users: one(users, {
    fields: [questions.userId],
    references: [users.id],
    relationName: "questions_users",
  })
}))
